.textInptDiv {
  width: -moz-fit-content;
  width: fit-content;
}
.textInptDiv__labelDiv {
  margin-bottom: 7px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.textInptDiv__labelDiv__label {
  font-size: 12px;
  font-weight: 700;
}
.textInptDiv__labelDiv__error {
  font-size: 12px;
  font-weight: 700;
  color: red;
}
.textInptDiv__inpt {
  padding: 1em 1.5em;
  border-radius: 7.5px;
  border: 2px solid #f1f1f1;
  font-weight: 700;
  width: 100%;
}
.textInptDiv__inpt:focus {
  outline: none;
  border: 2px solid #fbaf85;
  font-weight: 700;
  caret-color: #fbaf85;
}
.textInptDiv__inpt:focus::-moz-placeholder {
  font-size: 0;
}
.textInptDiv__inpt:focus::placeholder {
  font-size: 0;
}
.textInptDiv__inpt::-moz-placeholder {
  font-size: 12px;
}
.textInptDiv__inpt::placeholder {
  font-size: 12px;
}
.textInptDiv__inpt-error {
  padding: 1em 1.5em;
  border-radius: 7.5px;
  border: 2px solid red;
}
.textInptDiv__inpt-error:focus {
  outline: none;
}
.textInptDiv__inpt-error:focus::-moz-placeholder {
  font-size: 0;
}
.textInptDiv__inpt-error:focus::placeholder {
  font-size: 0;
}
.textInptDiv__inpt-error::-moz-placeholder {
  font-size: 12px;
}
.textInptDiv__inpt-error::placeholder {
  font-size: 12px;
}

.numberInptDiv {
  background-color: #f1f1f1;
  width: 100px;
  padding: 1em;
  display: flex;
  justify-content: space-between;
  font-size: 15px;
}
.numberInptDiv:hover {
  cursor: default;
}
.numberInptDiv__minus, .numberInptDiv__plus {
  color: gray;
}
.numberInptDiv__minus:hover, .numberInptDiv__plus:hover {
  color: #fbaf85;
  font-weight: 700;
  cursor: pointer;
}
.numberInptDiv__number {
  font-weight: 700;
}
.numberInptDiv__number:hover {
  cursor: default;
}

.radioInptDiv {
  border: 2px solid #f1f1f1;
  font-weight: 700;
  display: flex;
  align-items: center;
  padding: 1em 1.5em;
  border-radius: 7.5px;
}
.radioInptDiv__label {
  font-size: 12px;
}
.radioInptDiv__inpt {
  margin-right: 20px;
  width: 16px;
  height: 16px;
  accent-color: #d87d4a;
}
.radioInptDiv-checked {
  border: 2px solid #d87d4a;
}
.radioInptDiv__inpt:hover, .radioInptDiv__label:hover {
  cursor: pointer;
}
.radioInptDiv:hover {
  border: 2px solid #fbaf85;
  cursor: pointer;
}

.dropDownDiv__dropDown {
  padding: 1em 1.5em;
  border-radius: 7.5px;
  border: 2px solid #f1f1f1;
  font-weight: 700;
}
.dropDownDiv__dropDown:focus {
  outline: none;
  border: 2px solid #fbaf85;
  font-weight: 700;
  caret-color: #fbaf85;
}
.dropDownDiv__dropDown:focus::-moz-placeholder {
  font-size: 0;
}
.dropDownDiv__dropDown:focus::placeholder {
  font-size: 0;
}
.dropDownDiv__dropDown::-moz-placeholder {
  font-size: 12px;
}
.dropDownDiv__dropDown::placeholder {
  font-size: 12px;
}
.dropDownDiv__label {
  font-size: 12px;
  font-weight: 700;
  display: block;
  margin-bottom: 7px;
}

.navBar {
